import { FC } from 'react';
import classNames from 'classnames';
import { Product } from '../../../types';
import { addToCart } from '../../../api/commerce';
import prompt from '../prompt/prompt';
import Button from '../button/button';
import { useAppDispatch } from '../../../app/hooks';

const min = 0;

interface Props {
	product: Product;
}
const ProductCard: FC<Props> = ({
	product: {
		name,
		id,
		media: { source },
		price: { raw: price, formatted },
		inventory: { available }
	}
}) => {
	const dispatch = useAppDispatch();
	const handleAddtoCart = async () => {
		const amount = await prompt('Wprowadź ilość', {
			type: 'number',
			defaultValue: min.toString(),
			inputOptions: { min, max: available },
			checkIfGoodInput: (value) => {
				const number = parseInt(value, 10);
				return min < number && number <= available;
			},
			errorMessage: 'Błędna ilość!'
		});
		await addToCart(id, parseInt(amount, 10), dispatch);
	};
	const disabled = available === 0;
	return (
		<div className="product popup-catch appear-opacity">
			<h1 className="popup-receive">{name}</h1>
			<div className="product__content">
				<img
					src={source}
					alt={name}
					width="169"
					height="177"
					className="product__img popup-receive"
				/>
				<aside>
					<div className="product__available">{available} sztuk</div>
					<div className="product__price">{`${formatted} zł`}</div>
					<Button
						onClick={handleAddtoCart}
						className={classNames('product__btn', { 'popup-receive': !disabled })}
						disabled={disabled}>
						Do koszyka
					</Button>
				</aside>
			</div>
		</div>
	);
};

export default ProductCard;
