/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import { confirmable, createConfirmation, ReactConfirmProps } from 'react-confirm';
import classNames from 'classnames';

import { FC, useState } from 'react';
import Input from '../input/input';
import Button from '../button/button';

import './prompt.css';

interface BaseProps {
	type: string;
	defaultValue?: string;
	inputOptions?: Record<string, unknown>;
	checkIfGoodInput?: (value: string) => boolean;
	errorMessage?: string;
}
interface NumberTypeProps extends BaseProps {
	type: 'number';
	inputOptions?: { min?: number; max?: number; step?: number };
}
type Props = (BaseProps | NumberTypeProps) & ReactConfirmProps & { confirmation: string };
const Prompt: FC<Props> = ({
	confirmation,
	proceed,
	dismiss,
	type,
	defaultValue = '',
	show,
	inputOptions,
	checkIfGoodInput = () => true,
	errorMessage = ''
}) => {
	const [value, setValue] = useState(defaultValue);
	const [isError, setIsError] = useState(false);
	const handleProceed = () => {
		const isGood = checkIfGoodInput(value);
		if (isGood) proceed(value);
		else setIsError(!isGood);
	};
	return (
		<div className="prompt-background" onClick={dismiss} style={show ? {} : { display: 'none' }}>
			<div className="prompt" onClick={(e) => e.stopPropagation()}>
				<Input
					id="prompt__input"
					type={type}
					label={confirmation}
					containerClass={classNames('prompt__input', { 'input--error': isError })}
					valueSetValue={[value, setValue]}
					inputOptions={inputOptions}
				/>
				{errorMessage && <div className="prompt__error">{isError && errorMessage}</div>}
				<div className="prompt__buttons">
					<Button onClick={dismiss}>Anuluj</Button>
					<Button onClick={handleProceed}>Okej</Button>
				</div>
			</div>
		</div>
	);
};
type PromptFunc = { (confirmation: string, props: BaseProps | NumberTypeProps): Promise<string> };
const prompt: PromptFunc = (confirmation, props) => {
	const confirm = createConfirmation(confirmable(Prompt));
	return confirm({ confirmation, ...props });
};

export default prompt;
