import classNames from 'classnames';
import { FC } from 'react';
import './border.css';

interface Props {
	parentClass: string;
	className?: string;
	noBg?: boolean;
}

const FancyBorder: FC<Props> = ({ children, parentClass, className, noBg = false }) => {
	if (noBg)
		return <div className={classNames('fancy-border', parentClass, className)}>{children}</div>;
	return (
		<div className={classNames('fancy-border', parentClass, className)}>
			<div className={`fancy-border__background ${parentClass}__background`}>{children}</div>
		</div>
	);
};

export default FancyBorder;
