import { FC } from 'react';
import { CartItem } from '../../types';
import './cart.css';
import FancyBorder from '../common/fancy-border/border';
import Button from '../common/button/button';
import { removeFromCart, updateCartQty } from '../../api/commerce';
import { useAppDispatch } from '../../app/hooks';

interface Props {
	product: CartItem;
}

const CartCard: FC<Props> = ({ product }) => {
	const {
		id,
		name,
		media: { source },
		price: { raw },
		quantity
	} = product;
	const dispatch = useAppDispatch();
	return (
		// <div className="cart-card border-gradient">
		<FancyBorder parentClass="cart-card">
			<h2 className="cart-card__title">{name}</h2>
			<article className="cart-card__content flex">
				<div className="cart-card__left flex cart-card__flex">
					<div className="flex flex--vertical cart-card__flex cart-card__quantity-widget">
						<FancyBorder noBg parentClass="cart-card__arrow" className="popup">
							<Button
								onClick={() => updateCartQty(id, quantity + 1, dispatch)}
								className="arrow__button fancy-border__btn"
								noPopup>
								︿
							</Button>
						</FancyBorder>
						<div className="cart-card__quantity">{`x${quantity}`}</div>
						{/* TODO: To Button */}
						<FancyBorder noBg parentClass="cart-card__arrow" className="popup">
							<Button
								onClick={() => updateCartQty(id, quantity - 1, dispatch)}
								className="arrow__button fancy-border__btn"
								noPopup>
								﹀
							</Button>
						</FancyBorder>
						{/* <div className="cart-card__arrow">﹀</div> */}
					</div>
					<img src={source} width="62" height="65" alt="" className="cart-card__img" />
				</div>
				<div className="cart-card__right flex cart-card__flex">
					<div className="cart-card__price">{`${(quantity * raw).toFixed(2)}zł`}</div>
					<Button onClick={() => removeFromCart(id, dispatch)} className="cart-card__cancel" noBg>
						×
					</Button>
				</div>
			</article>
		</FancyBorder>
		// </div>
	);
};

export default CartCard;
