import { FC } from 'react';
import classNames from 'classnames';
import './stepper.css';

interface StepperProps {
	steps: string[];
	active: number;
	className?: string;
}
interface StepProps {
	title: string;
	number: number;
	isCurrent: boolean;
}
const Step: FC<StepProps> = ({ title, number, isCurrent }) => (
	<div className={classNames('stepper__step', { 'stepper__step--current': isCurrent })}>
		<div className="step__title">{title}</div>
		<div className="step__number">{number}</div>
	</div>
);
const Line: FC = () => <div className="stepper__line" />;
const Stepper: FC<StepperProps> = ({ steps, active, className=""}) => {
	const elementsCount = 2 * steps.length - 1;
	const elements = [];
	for (let i = 0; i < elementsCount; i++) {
		const stepNumber = Math.ceil(i / 2);
		const title = steps[stepNumber];
		elements.push(
			i % 2 ? (
				<Line key={i} />
			) : (
				<Step key={i} isCurrent={stepNumber === active} title={title} number={stepNumber + 1} />
			)
		);
	}
	return <div className={`stepper flex ${className}`}>{elements}</div>;
};

export default Stepper;
