import { FC } from 'react';
import { Cart } from '../../types';
import CartCard from './cart-card';

interface Props {
	cartItems: Cart['line_items'];
}

const CartGallery: FC<Props> = ({ cartItems }) => {
	return (
		<section className="cart-gallery">
			{cartItems.map((item) => (
				<CartCard product={item} key={item.id} />
			))}
		</section>
	);
};

export default CartGallery;
