import { FC } from 'react';
import Input from '../common/input/input';
import Stepper from '../common/stepper/stepper';
import './checkout.css';

const Checkout: FC = () => {
	return (
		<main className="checkout paper">
			<Stepper active={1} steps={['Adres dostawy', 'Płatność']} className="checkout__stepper" />
			<div className="checkout__row">
				<Input id="checkout__name" label="Imię" type="text" containerClass="checkout__input-container" placeholder="placeholder"/>
				<Input id="checkout__surname" label="Nazwisko" type="text" containerClass="checkout__input-container"/>
			</div>
		</main>
	);
};

export default Checkout;
