import { FC } from 'react';
import { useAppDispatch, useAppSelector } from '../../app/hooks';
import CartGallery from './cart-gallery';
import Button from '../common/button/button';

import './cart.css';
import { emptyCart } from '../../api/commerce';
import { Cart } from '../../types';

const Summary: FC<{ cart: Cart }> = ({ cart }) => {
	const dispatch = useAppDispatch();
	return (
		<article className="cart__summary flex flex--vertical">
			<h2>Podsumowanie</h2>
			<div className="summary__price">{`${cart.subtotal.formatted}zł`}</div>
			<div className="summary__buttons flex">
				<Button className="summary__btn" onClick={() => emptyCart(dispatch)}>
					Wyczyść
				</Button>
				<Button className="summary__btn" isLink to="/checkout">
					Przejdź do kasy
				</Button>
			</div>
		</article>
	);
};
const CartComp: FC = () => {
	const cart = useAppSelector(({ cart: _cart }) => _cart);
	return (
		<main className="cart">
			<h1>Koszyk</h1>
			<Summary cart={cart} />
			<CartGallery cartItems={cart.line_items} />
		</main>
	);
};

export default CartComp;
