/* eslint-disable react/jsx-props-no-spreading */
import classNames from 'classnames';
import { ReactNode, FC, MouseEventHandler } from 'react';
import { Link } from 'react-router-dom';

interface BaseProps {
	className?: string;
	children: ReactNode;
	disabled?: boolean;
	noBg?: boolean;
	noPopup?: boolean;
}
interface LinkProps extends BaseProps {
	isLink: true;
	to: string;
}
interface ButtonProps extends BaseProps {
	onClick: MouseEventHandler<HTMLButtonElement>;
}
const DefaultButton: FC<ButtonProps> = ({
	children,
	onClick,
	className,
	disabled,
	noBg,
	noPopup
}) => {
	return (
		<button
			type="button"
			onClick={onClick}
			disabled={disabled}
			className={classNames(
				'btn',
				{ popup: !(disabled || noPopup), 'btn--no-bg': noBg },
				className
			)}>
			{children}
		</button>
	);
};
const Button: FC<ButtonProps | LinkProps> = (props) => {
	const { className, disabled, noBg, noPopup } = props;
	const newClass = {
		className: classNames('btn', { popup: !(disabled || noPopup), 'btn--no-bg': noBg }, className)
	};

	if ('isLink' in props) {
		if ('onClick' in props) throw new Error('onClick in a button that is a link!');
		return <Link {...props} {...newClass} />;
	}
	return <DefaultButton {...props} {...newClass} />;
};

export default Button;
