import classNames from 'classnames';
import { FC, useState } from 'react';

import './input.css';

interface InputOptions {
	min?: number;
	max?: number;
	step?: number;
}

interface Props {
	type: string;
	id: string;
	label: string;
	containerClass?: string;
	defaultInput?: string;
	inputOptions?: InputOptions;
	labelIn?: boolean;
	placeholder?: string;
	// noPopup?: boolean;
}
type UseStateArr = [string, React.Dispatch<React.SetStateAction<string>>];
type InputChoiceProps = Props & { valueSetValue?: UseStateArr };
type ExternalControlledProps = Props & { valueSetValue: UseStateArr };

const InputHtml: FC<ExternalControlledProps> = ({
	id,
	label,
	type,
	valueSetValue,
	containerClass,
	inputOptions,
	labelIn,
	placeholder=''
	// noPopup
}) => {
	const [value, setValue] = valueSetValue;
	return (
		<div
			className={classNames('input-container appear-opacity appear-scale', containerClass, {
				filled: value,
				'input-container--label-in': labelIn
				// popup: !noPopup
			})}>
			<input
				type={type}
				id={id}
				className="input"
				value={value}
				onChange={(e) => setValue(e.target.value)}
				placeholder={placeholder}
				// eslint-disable-next-line react/jsx-props-no-spreading
				{...inputOptions}
			/>
			<label htmlFor={id}>{label}</label>
		</div>
	);
};
const ControlledInput: FC<Props> = (props) => {
	const { defaultInput = '' } = props;
	const valueSetValue = useState(defaultInput);
	// eslint-disable-next-line react/jsx-props-no-spreading
	return <InputHtml {...props} valueSetValue={valueSetValue} />;
};
const Input: FC<InputChoiceProps> = (props) => {
	if ('valueSetValue' in props) {
		// eslint-disable-next-line react/jsx-props-no-spreading
		return <InputHtml {...(props as ExternalControlledProps)} />;
	}
	// eslint-disable-next-line react/jsx-props-no-spreading
	return <ControlledInput {...props} />;
};
export default Input;
