import { CSSProperties } from 'react';

export enum DelayType {
	both,
	animation,
	transition,
	bothTransition
}
export enum FetchStatus {
	pending,
	done,
	error
}
interface Attributes {
	type?: DelayType;
	staggerStep: number;
	nodeIndex: number;
	nodesTotal: number;
	delay?: number;
	reverse?: boolean;
	skip?: boolean;
}
export const calcDelay = ({
	type = DelayType.bothTransition,
	staggerStep,
	nodeIndex,
	nodesTotal,
	delay = 0,
	reverse = false,
	skip = false
}: Attributes): CSSProperties => {
	const calcVal = `${
		skip ? 0 : delay + (reverse ? nodesTotal - nodeIndex - 1 : nodeIndex) * staggerStep
	}s`;
	const calcValSoft = `${nodeIndex * staggerStep}s`;
	// eslint-disable-next-line prettier/prettier
	// if (type === DelayType.both)
	switch (type) {
		case DelayType.both:
			return { animationDelay: calcVal, transitionDelay: calcVal };
		case DelayType.animation:
			return { animationDelay: calcVal };
		case DelayType.transition:
			return { transitionDelay: calcVal };
		default:
		case DelayType.bothTransition:
			return { transitionDelay: calcVal, animationDelay: calcValSoft };
	}
};
