import Commerce from '@chec/commerce.js';
import { useEffect } from 'react';
import { toast } from 'react-toastify';
import { useAppDispatch } from '../app/hooks';
import { AppDispatch } from '../app/store';
import { setCart } from '../features/cart/cart';
import { productsFetched } from '../features/products/products';

const commerce = new Commerce(process.env.REACT_APP_CHEC_PUBLIC_KEY, true);
type DispatchSetCart = AppDispatch;
// console.log(commerce);

export const useCommerceStore = (): void => {
	const dispatch = useAppDispatch();
	useEffect(() => {
		const fetchData = async () => {
			try {
				const response = await commerce.products.list();

				const { data: productsData } = response;
				const cartData = await commerce.cart.retrieve();
				dispatch(productsFetched(productsData));
				dispatch(setCart(cartData));
			} catch {
				toast('Błąd połączenia z serwerem', { type: 'error' });
			}
		};
		fetchData();
	}, [dispatch]);
};

export const addToCart = async (
	productId: string,
	quantity = 1,
	dispatch: DispatchSetCart
): Promise<void> => {
	const { cart } = await commerce.cart.add(productId, quantity);
	// return cart;
	dispatch(setCart(cart));
};
export const updateCartQty = async (
	productId: string,
	quantity: number,
	dispatch: DispatchSetCart
): Promise<void> => {
	const { cart } = await commerce.cart.update(productId, { quantity });
	// return cart;
	dispatch(setCart(cart));
};
export const removeFromCart = async (
	productId: string,
	dispatch: DispatchSetCart
): Promise<void> => {
	const { cart } = await commerce.cart.remove(productId);
	// return cart;
	dispatch(setCart(cart));
};
export const emptyCart = async (dispatch: DispatchSetCart): Promise<void> => {
	const { cart } = await commerce.cart.empty();
	// return cart;
	dispatch(setCart(cart));
};
